.simple-marker {
  background-image: url('./simpleMarker.svg');
  background-repeat: no-repeat;
  position: absolute;
  cursor: pointer;
  background-size: contain;
  width: 49px;
  height: 64px;
  top: -49px;
  left: -32px;
  -webkit-transform-origin: -24.5px;
      -ms-transform-origin: -24.5px;
          transform-origin: -24.5px;
  margin: 0;
  padding: 0;
}

.cluster-marker {
  position: absolute;
  cursor: pointer;
  width: 40px;
  height: 40px;
  color: black;
  font-size: 18px;
  background: white;
  border: 5px solid #004336;
  padding: none;
  margin: none;
  display: -ms-inline-flexbox;
  display: inline-flex;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  border-radius: 50%; 
  -webkit-transform: translate(-50%, -50%); 
      -ms-transform: translate(-50%, -50%); 
          transform: translate(-50%, -50%);
}



